/**
*
* List
*
**/
ul,
ol {
  list-style: none;
  margin: 0; }
  @media only screen and (max-width: 619px) {
    ul,
    ol {
      padding-left: 0; } }

ul li {
  position: relative;
  padding-left: 16px; }
  ul li::before {
    font-family: "icons";
    font-size: 13px;
    display: block;
    font-weight: 400;
    position: absolute;
    top: 0;
    left: 0;
    line-height: 26px; }
  ul li::before {
    font-size: 4px; }

ol {
  counter-reset: list; }
  ol > li {
    position: relative;
    padding-left: 32px; }
    ol > li::before {
      counter-increment: list;
      content: "0" counter(list);
      color: inherit;
      font-weight: 400;
      display: inline-block;
      position: absolute;
      left: 0; }
    ol > li:nth-child(10n) ~ li::before, ol > li:nth-child(10n)::before {
      content: counter(list); }

ul ol,
ol ul {
  padding-top: 0; }

.definition-list dd {
  display: inline;
  margin: 0; }

.definition-list dd:after {
  display: block;
  content: ''; }

.definition-list dt {
  display: inline-block;
  min-width: 250px;
  font-weight: 400; }

/**
*
* Media - imgs/videos
*
**/
img,
video,
object {
  max-width: 100%; }

.content img {
  margin-top: 26px;
  margin-bottom: 26px; }

/**
*
* Helper
*
**/
.clear::before, .clear::after {
  content: '';
  display: table; }

.clear::after {
  clear: both; }

